import React from 'react'
import './task.css'
import './hover.css'
import IconButton from '@material-ui/core/IconButton';
function Cards({logo,text}) {
    return (
        
                <IconButton className="curve text-white hoverChange d-flex flex-column mx-3 px-4" style={{backgroundColor:"#1A1A1E"}}>
                    <div className=" hover d-flex flex-column">
                    <i className={`${logo} text-primary mb-2`}></i>
                       <span style={{fontSize:"10px"}}>{text}</span>
                    </div>
                  
                 </IconButton>
               
          
           

   
    )
}

export default Cards
