import React from 'react'
import NightsStayRounded from '@material-ui/icons/NightsStayRounded';
import IconButton from '@material-ui/core/IconButton';
import TimelineIcon from '@material-ui/icons/Timeline';
function Head({text}) {
    return (
        <header className="d-flex justify-content-between py-4 px-2 ">
            <div className="text-white">
               <h6 >{text}</h6>
               <p><small className="text-muted">Lorem, ipsum dolor sit amet consecte?</small></p>
            </div>

            <div>
                <IconButton><NightsStayRounded className="rounded p-1 bg-white text-dark"/></IconButton>
                <IconButton><TimelineIcon className="rounded p-1 bg-primary text-white"/></IconButton>
            </div>
        </header>
    )
}

export default Head
