import React from 'react'
import './task.css'

function Task({text}) {
    return (
        <div className="curve card text-white text-center  px-4 m-2 d-flex" style={{backgroundColor:"#1A1A1E"}}>
        
        <p><input type="checkbox" className="mx-2"/><small>{text}</small><i className="fa fa-user-circle m-2"></i></p>
       

    </div>
    )
}

export default Task
